import React from 'react';
import { withComponentFeatures } from 'universal-dashboard';
import Trend from 'react-trend';

const UDTrendLine = props => {
  return <Trend
  smooth={props.smooth}
  autoDraw={props.autoDraw}
  autoDrawDuration={props.autoDrawDuration}
  autoDrawEasing={props.autoDrawEasing}
  data={props.data}
  gradient={props.gradient}
  radius={props.radius}
  strokeWidth={props.strokeWidth}
  strokeLinecap={props.strokeLinecap}
/>
}

export default withComponentFeatures(UDTrendLine)